import api from '../../plugins/axios'

const searchScope = {
  state: {
    searchProducts: [],
    searchProductsLoaded: false,
    searchCategories: [],
    suggestionScope: [],
    searchBrand: []
  },
  getters: {
    searchProducts (state) {
      return state.searchProducts
    },
    searchProductsLoaded (state) {
      return state.searchProductsLoaded
    },
    searchCategories (state) {
      return state.searchCategories
    },
    suggestionScope (state) {
      return state.suggestionScope
    },
    searchBrand (state) {
      return state.searchBrand
    }
  },
  mutations: {
    setSearchProducts (state, products) {
      state.searchProducts = products
      state.searchProductsLoaded = true
    },
    includeSearchBrands (state, brands) {
      state.searchBrand = brands
      for (const brandName of brands) {
        state.suggestionScope.push({
          name: brandName,
          type: 'brand'
        })
      }
    },
    setSearchCategories (state, categories) {
      state.searchCategories = categories
      for (const category of categories) {
        state.suggestionScope.push({
          name: category.name,
          type: 'category'
        })
        for (const subCategoryName of category.subCategories) {
          state.suggestionScope.push({
            name: subCategoryName,
            category: category.name,
            type: 'subCategory'
          })
        }
      }
    }
  },
  actions: {
    loadSearchScope (context) {
      // let names = sessionStorage.getItem('allNames')
      // if (names) {
      //   names = JSON.parse(names)
      //   context.commit('setSearchProducts', names.products)
      //   context.commit('includeSearchBrands', names.brands)
      //   context.commit('setSearchCategories', names.categories)
      // } else {
      api.get('all-names')
        .then(response => {
          console.log('search scope loaded')
          console.log(response)
          // sessionStorage.setItem('allNames', JSON.stringify(response.data))
          context.commit('setSearchProducts', response.data.products)
          context.commit('includeSearchBrands', response.data.brands)
          context.commit('setSearchCategories', response.data.categories)
        })
        .catch(error => {
          console.log('search scope loading failed')
          console.log(error.response)
        })
      // }
    }
  }
}

export default searchScope
